<template>
  <section class="newsletter">
    <h2>Suscribe to our Newsletter !</h2>
    <form @submit.prevent="suscribe">
      <input name="email" type="email" placeholder="Your e-mail" v-model="email" />
      <div class="submit">
        <button class="anim" type="submit">SUSCRIBE</button>
      </div>
    </form>
    <p>Get & follow the progress for all our projects.</p>
  </section>
</template>

<script>
export default {
  name: "NewsletterComp",
  data() {
    return {
      email: null,
    }
  },
  methods: {
    suscribe() {
      if (!this.email) alert("Empty email. You must send a valid e-mail to submit.")
      else alert(`Thank you "${this.email}" for suscribing !`)
    }
  }
}
</script>

