<script>
export default {
  name: 'ProgressbarComp',
  methods: {
    progressbar() {
      /*
      //const scrolled = (document.body.scrollTop || document.documentElement.scrollTop)
      const scrollable = document.documentElement.scrollHeight - document.documentElement.clientHeight // <- total doc height - total screen height
      //calcProgress = (scrolled / scrollable) * 100
      */
      const progressbar = document.querySelector('.progress-line')
      const elmtToTop = document.querySelector('.progress-bar').getBoundingClientRect().top
      const screenSize = document.documentElement.clientHeight
      const elmtSize = document.querySelector('.progress-container').getBoundingClientRect().height
      let calcProgress =  ((elmtSize - elmtToTop) / screenSize) * 100
      if (window.innerWidth >= 1200) {
        calcProgress += 40
      } else {
        calcProgress -= 40
      }
      if (calcProgress > 100) return
      progressbar.style.height = `${calcProgress}%`
    }
  },
  mounted() {
    const progressbarContainerHeight = this.$refs.container.getBoundingClientRect().height
    document.querySelector('.progress-container').style.height = `${progressbarContainerHeight}px`
    window.addEventListener('scroll', this.progressbar)
  },
  unmounted() {
    window.removeEventListener('scroll', this.progressbar)
  },
}
</script>

