<template>
  <nav>
    <div class="nav">
      <div class="logo">
        <router-link to="/"><img src="../assets/beelieve.png" alt="logo"></router-link>
      </div>
      <div class="router">
        <button class="s" @click="openMenu"><i class="fas fa-bars"></i></button>
        <router-link class="l" to="/">Homepage</router-link>
        <router-link class="l" to="/projects">Projects</router-link>
        <router-link class="l" to="/gallery">Gallery</router-link>
        <router-link class="l" to="/nft">NFT</router-link>
        <router-link class="l" to="/about">About</router-link>
      </div>    
    </div>      
  </nav>
  <div v-if="menu" class="grayscale" @click="closeMenu()"></div>
  <aside v-if="menu">    
    <div class="aside-menu">
      <router-link to="/" @click="closeMenu()">Homepage</router-link>
      <router-link to="/projects" @click="closeMenu()">Projects</router-link>
      <router-link to="/gallery" @click="closeMenu()">Gallery</router-link>
      <router-link to="/nft" @click="closeMenu()">NFT</router-link>
      <router-link to="/about" @click="closeMenu()">About</router-link>
    </div>
  </aside>
</template>

<script>
export default {
  name: 'NavComp',
  data() {
    return {
      menu: false
    }
  },
  methods: {
    openMenu() {
      this.menu = !this.menu
      this.menu ? document.querySelector('body').style.overflow = "hidden" : document.querySelector('body').style.overflow = "auto"
    },
    closeMenu() {
      this.menu = false
      document.querySelector('body').style.overflow = "auto"
    }
  }
}
</script>

