<template>
  <NavComp />
  <BeehiveComp />
  <TestimonialsComp />
  <ProgressbarComp />
  <FooterComp />
</template>

<script>
import NavComp from '@/components/NavComp.vue'
import BeehiveComp from '@/components/BeehiveComp.vue'
import TestimonialsComp from '@/components/TestimonialsComp.vue'
import ProgressbarComp from '@/components/ProgressbarComp.vue'
import FooterComp from '@/components/FooterComp.vue'

export default {
  name: 'HomeView',
  components: {
    NavComp,
    BeehiveComp,
    TestimonialsComp,
    ProgressbarComp,
    FooterComp
  },
  methods: {
    intersectionObserverAPI() {
      const options = {
        root: null,
        rootMargin: "0px",
        threshold: 0.9,
      };
      function handleIntersect(entries, observer) {
        entries.forEach((entry) => {
          if (entry.intersectionRatio > options.threshold) {
            entry.target.style.animation = "anim 1s linear";
            entry.target.style.opacity = "1";
            observer.unobserve(entry.target);
          }
        });
      }
      const observer = new IntersectionObserver(handleIntersect, options);
      const elmts = document.querySelectorAll(".anim");
      elmts.forEach((elt) => {
        observer.observe(elt);
      });
    },
  },
  mounted() {
    this.intersectionObserverAPI();
  }
}
</script>
